import React from 'react';

const CommentsList = ({ comments }) => (
   <>
   <h3>Comments:</h3>
   {typeof(comments) === 'object' ? comments.map((comment, key) => (
      <div className="comment" key={key}>
         <h4>{comment.username}</h4>
         <span>{comment.text}</span>
      </div>
   )) : null}
   </>
)

export default CommentsList;
